.loader {
  width: 80vw;
  height: 3vh;
  margin: 0 auto;
	position: relative;
	top: 3vh;
	z-index: 6;
}
.dummy {
  height: 3vh;
}
.dot{
  position: absolute;	
  background-image: url('../Ball/Ball.png');
  background-size: cover;
  width: 3vw;
  height: 3vw;
	-webkit-animation: 3s linear loader infinite;
	        animation: 3s linear loader infinite;
}
.dot:nth-of-type(1) {
	left: 0px;
}
.dot:nth-of-type(2) {
	left: -10px;
	-webkit-animation-delay: 0.1s;
	        animation-delay: 0.1s;
}
.dot:nth-of-type(3) {
	left: -20px;
	-webkit-animation-delay: 0.2s;
	        animation-delay: 0.2s;	
}
.dot:nth-of-type(4) {
	left: -30px;
	-webkit-animation-delay: 0.3s;
	        animation-delay: 0.3s;	
}
.dot:nth-of-type(5) {
	left: -40px;
	-webkit-animation-delay: 0.4s;
	        animation-delay: 0.4s;	
}
.dot:nth-of-type(6) {
	left: -50px;
	-webkit-animation-delay: 0.50s;
	        animation-delay: 0.50s;	
}
.dot:nth-of-type(7) {
	left: -60px;
	-webkit-animation-delay: 0.60s;
	        animation-delay: 0.60s;	
}
.dot:nth-of-type(8) {
	left: -70px;
	-webkit-animation-delay: 0.7s;
	        animation-delay: 0.7s;	
}
.dot:nth-of-type(9) {
	left: -80px;
	-webkit-animation-delay: 0.8s;
	        animation-delay: 0.8s;	
}
.dot:nth-of-type(10) {
	left: -90px;
	-webkit-animation-delay: 0.9s;
	        animation-delay: 0.9s;	
}
@-webkit-keyframes loader {
	0%, 20% {
			-webkit-transform: translate(0, -50%);
			        transform: translate(0, -50%);
			opacity: 0;
	}
	35% {
		-webkit-transform: translate(45vw, -50%);
		        transform: translate(45vw, -50%);
		opacity: 1;
	}
	65% {
		-webkit-transform: translate(55vw, -50%);
		        transform: translate(55vw, -50%);
		opacity: 1;
	}
	80%, 100% {
			-webkit-transform: translate(100vw, -50%);
			        transform: translate(100vw, -50%);
			opacity: 0;
	}
}
@keyframes loader {
	0%, 20% {
			-webkit-transform: translate(0, -50%);
			        transform: translate(0, -50%);
			opacity: 0;
	}
	35% {
		-webkit-transform: translate(45vw, -50%);
		        transform: translate(45vw, -50%);
		opacity: 1;
	}
	65% {
		-webkit-transform: translate(55vw, -50%);
		        transform: translate(55vw, -50%);
		opacity: 1;
	}
	80%, 100% {
			-webkit-transform: translate(100vw, -50%);
			        transform: translate(100vw, -50%);
			opacity: 0;
	}
}


